export const calculateYearShift = (
    birthYear: number,
    startYear: number,
    len: number
) => {
    let acc = birthYear;

    while (acc < startYear) {
        acc += len;
    }

    return len - (acc - startYear);
};

export const makeRepeated = (arr: number[], repeats: number) =>
    Array.from({ length: repeats }, () => arr).flat();
export const getMonthNums = (num: number): string => {
    if (num === 0) {
        return '';
    }

    return `${num} ${declination(num, ['месяц', 'месяцев', 'месяцев'])}`;
}
export const getYearsNums = (num: number): string => {
    return `${num} ${declination(num, ['год', 'года', 'лет'])}`;
}
const declination = (number: number, titles: string[]): string => {
    const cases = [2, 0, 1, 1, 1, 2];
    return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
}